import React, {FormEvent} from 'react';
import {Chip, FormControl, Input, MenuItem, Select} from "@material-ui/core";
import './App.css';
import {exportComponentAsPNG} from 'react-component-export-image';
import Box from "@material-ui/core/Box";
import {SelektorGminy} from "./SelektorGminy";
import {Panel} from "./Panel";

interface AppProps {
}

interface AppState {
    wojewodztwo: string;
    cytat: string;
    autor: string;
    select: string[];
    kodGminy: string;
}

class App extends React.Component<AppProps, AppState> {
    classes : any;
    componentRef : any;

    wojewodztwa = [
        "DOLNOŚLĄSKIE",
        "KUJAWSKO-POMORSKIE",
        "LUBELSKIE",
        "LUBUSKIE",
        "ŁÓDZKIE",
        "MAŁOPOLSKIE",
        "MAZOWIECKIE",
        "OPOLSKIE",
        "PODKARPACKIE",
        "PODLASKIE",
        "POMORSKIE",
        "ŚLĄSKIE",
        "ŚWIĘTOKRZYSKIE",
        "WARMIŃSKO-MAZURSKIE",
        "WIELKOPOLSKIE",
        "ZACHODNIOPOMORSKIE",
]

    elements = [
        "budzet",
        "smieci"
    ]

    constructor(props: AppProps) {
        super(props);
        this.componentRef = React.createRef();

        this.handleChangeAutor = this.handleChangeAutor.bind(this);
        this.handleChangeCytat = this.handleChangeCytat.bind(this);
        this.handleChangeMultiple = this.handleChangeMultiple.bind(this);

        this.handleChangeGmina = this.handleChangeGmina.bind(this);
        this.handleChangeWojewodztwo = this.handleChangeWojewodztwo.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);

        this.state = {
            cytat: "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Sed laoreet non tellus eget semper. Sed enim eros, mattis a feugiat ac, elementum vel magna. Etiam ut lacus congue tortor interdum dignissim quis in massa. Aenean libero lacus, molestie quis nisi eget, pellentesque consectetur mauris.",
            autor: "Autor",
            select: ["budzet", "smieci"],
            wojewodztwo: "MAŁOPOLSKIE",
            kodGminy: "1261011",
        };
    }

    handleChangeAutor(event: React.ChangeEvent<{ value: unknown }>) {
        if (event !== null && event.target !== null) {
            // @ts-ignore
            if (event.target.value !== null) {
                this.setState(Object.assign({}, this.state, {autor: event.target.value}));
            }
        } else {
            // no luck
        }
    }

    handleChangeCytat(event: React.ChangeEvent<{ value: unknown }>) {
        if (event !== null && event.target !== null) {
            // @ts-ignore
            if (event.target.value !== null) {
                this.setState(Object.assign({}, this.state, {cytat: event.target.value}));
            }
        } else {
            // no luck
        }
    }



    handleChangeMultiple(event: React.ChangeEvent<{ value: unknown }>) {
        console.log(event.target);
        const value: string[] = [];
        // @ts-ignore
        for (let i = 0, l = event.target.value.length; i < l; i += 1) {
            // @ts-ignore
            value.push(event.target.value[i]);
        }
        this.setState(Object.assign({}, this.state, {select: value}));
    }

    handleChangeWojewodztwo(event: React.ChangeEvent<{ value: unknown }>) {
        this.setState(Object.assign({}, this.state, {wojewodztwo: event.target.value}));
    }

    handleChangeGmina(kodGminy: string) {
        this.setState(Object.assign({}, this.state, {kodGminy: kodGminy}));
    }

    handleSubmit(event: FormEvent) {
        event.preventDefault();
    }

    //TODO: use redux
    render() {
        const { cytat, autor, select, wojewodztwo, kodGminy } = this.state;
        const elements = this.elements;
        const wojewodztwa = this.wojewodztwa;
        return (

            // https://www.npmjs.com/package/react-component-export-image
            <Box display="flex" flexDirection="row">
                    <Panel kodGminy={kodGminy} ref={this.componentRef} cytat={cytat} autor={autor} selected={select}/>
                    <form onSubmit={this.handleSubmit}>
                        <Box display="flex" flexDirection="column">
                            <strong>Wybór Jednostki Samorządu:</strong>
                            <select
                                id="demo"
                                value={wojewodztwo}
                                onChange={this.handleChangeWojewodztwo}>
                                {wojewodztwa.map((name) => (
                                    <option key={name} value={name}>
                                        {name}
                                    </option>
                                ))}
                            </select>
                            <SelektorGminy wojewodztwo={wojewodztwo} gminaChangeFn={this.handleChangeGmina} kodWybranejGminy={kodGminy}/>
                            <br/>
                            <strong>Komentarz eksperta:</strong>
                                <textarea name="cytat" onChange={this.handleChangeCytat} value={cytat}/>
                                <input type="text" name="autor" onChange={this.handleChangeAutor} value={autor}/>
                            <FormControl className="formControl">

                                <br/>
                                <strong>Elementy grafiki:</strong>
                                <Select
                                    labelId="demo-mutiple-chip-label"
                                    id="demo-mutiple-chip"
                                    multiple
                                    value={select}
                                    onChange={this.handleChangeMultiple}
                                    input={<Input id="select-multiple-chip" />}
                                    renderValue={(selected) => (
                                        <div className="chips">
                                            {(selected as string[]).map((value) => (
                                                <Chip key={value} label={value} className="chip" />
                                            ))}
                                        </div>
                                    )}
                                >
                                    {elements.map((name) => (
                                        <MenuItem key={name} value={name}>
                                            {name}
                                        </MenuItem>
                                    ))}
                                </Select>
                            </FormControl>
                            <button onClick={() => exportComponentAsPNG(this.componentRef)}>
                                Export PNG
                            </button>
                        </Box>
                    </form>
            </Box>
        );
    }
}

export default App;
