import React from "react";
import axios from "axios";

interface SelektorGminyProps {
    wojewodztwo: string;
    gminaChangeFn: (kodGminy: string) => void;
    kodWybranejGminy: string;
}

interface PowiatEls {
    gminy: Map<string, string>;
}

interface SelektorGminyState {
    wojewodztwo: string;
    powiaty: Map<string, PowiatEls>;
}

export class SelektorGminy extends React.Component<SelektorGminyProps, SelektorGminyState> {
    constructor(props: SelektorGminyProps) {
        super(props);
        this.state = {powiaty: new Map<string, PowiatEls>(), wojewodztwo: 'bla'};
    }

    render() {
        const {kodWybranejGminy, gminaChangeFn} = this.props;
        const {powiaty} = this.state;
        return <select
            id="demo"
            value={kodWybranejGminy}
            onChange={ev => gminaChangeFn(ev.target.value)}>
            {Array.from(powiaty.entries(), ([key, value]) => (
                    <optgroup key={key} label={key}>
                        {Array.from(value.gminy.entries(), ([gmina, kodGminy]) => (
                                <option key={kodGminy} value={kodGminy}>
                                    {gmina}
                                </option>
                            ))}
                    </optgroup>
            ))}
        </select>
    }

    common() {
        if (this.state.wojewodztwo !== this.props.wojewodztwo) {
            let wojName = this.props.wojewodztwo.replaceAll("Ś", "S").replaceAll("Ą", "A").replaceAll("Ń", "N").replaceAll("Ł", "L");
            const apiUrl = '/files/' + wojName + '.json';
            axios.get(apiUrl).then((response) => {
                const ret = new Map<string, PowiatEls>();
                for (const x in response.data) {
                    if (response.data.hasOwnProperty(x)) {
                        const gminy = new Map<string, string>();
                        for (const y in response.data[x]) {
                            if (response.data[x].hasOwnProperty(y)) {
                                gminy.set(y, response.data[x][y]);
                            }
                        }
                        const p: PowiatEls = {gminy: gminy};
                        ret.set(x, p);
                    }
                }
                this.setState(Object.assign({}, this.state, {powiaty: ret, wojewodztwo: this.props.wojewodztwo}));
            });
        }
    }

    componentDidMount() {
        this.common();
    }

    componentDidUpdate() {
        this.common();
    }
}